import UpsellControl from "@/builder/components/upsell-control";
import {
  LongTextControl,
  PanelBody,
  SpacingControl,
} from "@wptb/ui/components";
import { BlockControlProps } from "@wptb/dev/types";
import { CustomHtmlBlockProps } from "@wptb/dev/types";
import { useEffect, useRef, useState } from "react";
import DOMPurify from "dompurify";



const Control = ({
  blockProps,
  setBlockProps,
}: BlockControlProps<CustomHtmlBlockProps>) => {
  const changedHere = useRef(false);
  const [html, setHtml] = useState(blockProps.html);

  useEffect(() => {
    if (changedHere.current) {
      changedHere.current = false;
      return;
    }
    setHtml(blockProps.html);
  }, [blockProps.html]);

  const onBlur = () => {
    changedHere.current = true;
    const sanitized = DOMPurify.sanitize(html, {
      ADD_TAGS: ["iframe"],
      ADD_ATTR: ["allow", "allowfullscreen", "frameborder", "src"],
      FORBID_ATTR: ["on*"],
      FORBID_TAGS: ["script", "style", "object", "embed", "link"],
    });
    setBlockProps({ html: sanitized });
    setHtml(sanitized);
  };

  return (
    <PanelBody title="Custom HTML" className="wptb-settings" initialOpen>
      <LongTextControl
        label="HTML Content"
        value={html}
        onChange={setHtml}
        onBlur={onBlur}
      />
      <UpsellControl name="custom-html.padding">
        <SpacingControl label="Padding" value={blockProps?.padding} />
      </UpsellControl>
      <UpsellControl name="custom-html.margin">
        <SpacingControl label="Margin" value={blockProps?.margin} />
      </UpsellControl>
    </PanelBody>
  );
};

export default Control;
